@charset "UTF-8";
@import "assets/SCSS/style";

@font-face {
  font-family: 'Jost';
  src: local('./fonts/Jost-Regular.eot');
  src: url('assets/fonts/Jost-Regular.eot?#iefix') format('embedded-opentype'),
  url('assets/fonts/Jost-Regular.woff2') format('woff2'),
  url('assets/fonts/Jost-Regular.woff') format('woff'),
  url('assets/fonts/Jost-Regular.ttf') format('truetype'),
  url('assets/fonts/Jost-Regular.svg#Jost-Regular') format('svg');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Jost';
  src: local('./fonts/Jost-Bold.eot');
  src: url('assets/fonts/Jost-Bold.eot?#iefix') format('embedded-opentype'),
  url('assets/fonts/Jost-Bold.woff2') format('woff2'),
  url('assets/fonts/Jost-Bold.woff') format('woff'),
  url('assets/fonts/Jost-Bold.ttf') format('truetype'),
  url('assets/fonts/Jost-Bold.svg#Jost-Bold') format('svg');
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

* {
  padding: 0;
  margin: 0;
  border: 0;
}

*, *:before, *:after { /*- ширины блока для удобства*/
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
}

nav, footer, header, aside {
  display: block;
}

html, body { /*- запрещает самовольное изменение шрифта*/
  height: 100%;
  width: 100%;
  /*font-size: 100%;*/
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

/*Просим унаследовать семейство шрифта*/
input, button, textarea {
  font-family: inherit;
}

/*– будет тот шрифт который устанавливаем в “body”*/
/*Убираем по умолчанию стили в некоторых браузерах*/
input::-ms-clear {
  display: none;
}

/*– убираем крестик в form в браузере от MS */
button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a, a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

/*	 – убирает элементы списка*/
img {
  vertical-align: top;
}

/*Обнуление заголовков*/
h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  font-weight: 400;
  margin: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  //&::-webkit-scrollbar{
  //  display: none;
  //}
  //полоска прокрутки -----------------
  /* Работает в Firefox */
    scrollbar-width: thin;
    scrollbar-color: $headBcColor $colorH ;

  /* Работает в Chrome, Edge и Safari */
  &::-webkit-scrollbar {
    background-color: $colorH;
    width: 15px;
  }

  &::-webkit-scrollbar-track {
    background: transparent;
  }

  &::-webkit-scrollbar-thumb {
      background-color: $headBcColor;
      border-radius: 20px;
      border: 1px solid $colorH;

      &:hover{
        background-color: $colorP;
      }
  }
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

#root {
  height: 100%;
}

